.item-icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 64px;
  height: 64px;
}

.merge-board-cell {
  aspect-ratio: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 64px;
  height: 64px;
  transition: filter .2s;
  position: relative;
}

.merge-board-cell:hover {
  cursor: pointer;
  filter: brightness(120%);
}

.merge-board-cell--drop-target {
  filter: brightness(120%);
}

.merge-board-cell--drop-target .merge-board-cell__item, .merge-board-cell--drop-target .merge-board-cell__bubble {
  transform: translate(-8px, -8px)scale(1.05);
}

.merge-board-cell__background {
  background-image: url("public/images/BoardBackground.webp");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 200% 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.merge-board-cell__background--odd {
  background-position: 100% 0;
}

.merge-board-cell--dragging, .merge-board-cell--dragging:before {
  opacity: .3;
}

.merge-board-cell__bubble, .merge-board-cell__item {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 100%;
  transition: transform .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.merge-board-cell__bubble {
  background-image: url("public/images/Bubble.webp");
  background-position: 50% -80%;
  background-size: 150%;
}

.merge-board-cell__item--bubble {
  opacity: .8;
}

.merge-board-cell__item--hidden {
  opacity: .5;
}

.merge-board-cell__selection-indicator {
  width: 95%;
  height: 95%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.merge-board-cell__selection-indicator__tl, .merge-board-cell__selection-indicator__tr, .merge-board-cell__selection-indicator__bl, .merge-board-cell__selection-indicator__br {
  z-index: 900;
  width: 25%;
  height: 25%;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
  position: absolute;
}

.merge-board-cell__selection-indicator__tl {
  filter: drop-shadow(2px 2px #6c4756);
  border-top: 4px solid #ecebc9;
  border-left: 4px solid #ecebc9;
  border-top-left-radius: 4px;
  animation-name: float-tl;
  top: 0;
  left: 0;
}

.merge-board-cell__selection-indicator__tr {
  filter: drop-shadow(-2px 2px #6c4756);
  border-top: 4px solid #ecebc9;
  border-right: 4px solid #ecebc9;
  border-top-right-radius: 4px;
  animation-name: float-tr;
  top: 0;
  right: 0;
}

.merge-board-cell__selection-indicator__bl {
  filter: drop-shadow(2px -2px #6c4756);
  border-bottom: 4px solid #ecebc9;
  border-left: 4px solid #ecebc9;
  border-bottom-left-radius: 4px;
  animation-name: float-bl;
  bottom: 0;
  left: 0;
}

.merge-board-cell__selection-indicator__br {
  filter: drop-shadow(-2px -2px #6c4756);
  border-bottom: 4px solid #ecebc9;
  border-right: 4px solid #ecebc9;
  border-bottom-right-radius: 4px;
  animation-name: float-br;
  bottom: 0;
  right: 0;
}

.merge-board-cell__icons {
  pointer-events: none;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.merge-board-cell__icons__icon {
  filter: drop-shadow(2px 2px 2px #000);
}

@keyframes float-tl {
  0% {
    transform: translate(0);
  }

  100% {
    opacity: .8;
    transform: translate(-1.5px, -1.5px);
  }
}

@keyframes float-tr {
  0% {
    transform: translate(0);
  }

  100% {
    opacity: .8;
    transform: translate(1.5px, -1.5px);
  }
}

@keyframes float-bl {
  0% {
    transform: translate(0);
  }

  100% {
    opacity: .8;
    transform: translate(-1.5px, 1.5px);
  }
}

@keyframes float-br {
  0% {
    transform: translate(0);
  }

  100% {
    opacity: .8;
    transform: translate(1.5px, 1.5px);
  }
}

.merge-board {
  --mask: radial-gradient(4px at 4px 4px, #0000 98%, #000) -4px -4px;
  -webkit-mask: var(--mask);
  mask: var(--mask);
  align-content: flex-start;
  display: grid;
  position: relative;
  overflow: hidden;
}

.cell-editor-heading__item {
  width: 48px;
  height: 48px;
  margin-right: 8px;
}

.add-cell-heading {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.add-cell-heading__icon {
  margin-right: 8px;
}

.add-cell-preview {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.add-cell-preview__image {
  width: 48px;
  height: 48px;
  margin-right: 8px;
}

.cell-editor-tutorial {
  margin-bottom: 6px;
}

.merge-chain-vis {
  margin-top: 6px;
}

.merge-chain-vis__item {
  border: 1px dashed #0000;
  width: 48px;
  height: 48px;
  padding: 3px;
}

.merge-chain-vis__item--focused {
  border: 1px dashed #fff;
}

.merge-chain-vis__arrow {
  border-top: 18px solid #0000;
  border-bottom: 18px solid #0000;
  border-left: 18px solid #000;
  border-radius: 3px;
  width: 0;
  height: 0;
}

